.pagination-box {
  @media screen and (min-width: 768px) {
    width: 768px;
    margin-left: auto;
    margin-right: auto;
    padding-top: 50px;
    padding-bottom: 60px;
  }
  padding-bottom: 60px;
}
.tui-page-bt {
  width: 40px;
  height: 40px;
  display: flex;
  align-items: center;
  justify-content: center;
  color: black;
  font-family: Roboto;
  font-style: normal;
  font-weight: 500;
  font-size: 12px;
  line-height: 16px;
}
.tui-page-bt:hover {
  background-color: #ff6b08;
  border-radius: 5px;
  color: #f7f7f7;
}
.tui-is-selected {
  width: 40px;
  height: 40px;
  display: flex;
  align-items: center;
  justify-content: center;
  color: black;
  font-family: Roboto;
  font-style: normal;
  font-weight: 500;
  font-size: 12px;
  line-height: 16px;
  background-color: #ff6b08;
}
.tui-is-selected:focus {
  background-color: #ff6b08;
  border-radius: 5px;
  color: #f7f7f7;
}
.tui-page-btnn {
  display: flex;
  align-items: center;
  justify-content: center;
  color: black;
  width: 40px;
  height: 40px;
}
.tui-page-btnn:hover {
  background-color: #ff6b08;
  border-radius: 5px;
  color: #f7f7f7;
}
.tui-pagination .tui-page-btn {
  background-color: #f7f7f7;
  width: 40px;
  height: 40px;
}
.tui-first {
  background-image: url('data:image/png;base64,');
  background-size: 25px;
  background-repeat: no-repeat;
  background-position: 50%;
}
.tui-last {
  background-image: url('data:image/png;base64,');
  background-size: 25px;
  background-repeat: no-repeat;
  background-position: 50%;
}

.tui-pagination {
  height: 40px;
  display: flex;
  justify-content: center;
  margin: 40px 0 0 0;
  // @include min-width('medium') {
  //   margin: 50px 0 0 0;
  // }
}

.tui-page-btn.tui-prev.hide-prev,
.tui-page-btn.tui-first.tui-is-disabled,
.tui-page-btn.tui-next.hide-next,
.tui-page-btn.tui-last.tui-is-disabled,
.tui-page-btn.tui-next-is-ellip.change-next,
.tui-page-btn.tui-prev-is-ellip.change-prev {
  display: none;
}

