.tui-pagination .tui-next {
  background-image: url('data:image/png;base64,');
  background-size: 16px;
  background-repeat: no-repeat;
  background-position: 50%;
}

.tui-pagination .tui-prev {
  background-image: url('data:image/png;base64,');
  background-size: 16px;
  background-repeat: no-repeat;
  background-position: 50%;
}

.tui-pagination .tui-page-btn {
  display: flex;
  border: none;
  align-items: center;
  justify-content: center;
  padding: 0;
  width: 40px;
  height: 40px;
  border-radius: 5px;
  color: black;

  // @include font(12px, 500, 16px);
  // transition: all 250ms ease-in-out;
  &:hover {
    //   background-image: url('data:image/png;base64,');
    //   background-size: 16px;
    //   background-repeat: no-repeat;
    //   background-position: 50%;
    background-color: rgba(221, 221, 221, 0.473);
  }
}

.tui-pagination .tui-is-selected {
  color: #f7f7f7;
  background: #ff6b08;
  border-color: #ff6b08;
  border-radius: 5px;
}

.tui-pagination .hide-first,
.tui-pagination .hide-last {
  background: #f7f7f7;
  background: rgba(247, 247, 247, 0.5);
}

